import { Suppliers as sp,status } from './Types';
import { Requests, Token } from '../../Services';
import { request } from 'Services/Requests/StaticMethods';
import Url from 'Services/ServerUrl';

interface params{
    page        :  number,
    per_page    :  number,
}
export const Suppliers = (params ?:params) => {
        return async (dispatch:any) => {

            dispatch({
                type    : sp.data,
                payload : '',
            });

            if(!params){
                params.page     = 1;
                params.per_page = 10;
            }

            // dispatch({
            //     type    : sp,
            //     payload : await Requests.Suppliers.show(params),
            // });

            const token = Token.get();
            if(token === '' || token === null){
                dispatch({
                    type    : status.loggedIn,
                    payload : false,
                })
                return;
            }

            const a = await request({
                url     : Url.suppliers+'?page='+params.page+'&per_page='+params.per_page,
                method  : 'GET',
                params  : false,
            })


            if (a.status === 200) {
                dispatch({
                    type    : sp.data,
                    payload : a,
                });
                return;
            }

            if(a.network_error){
                dispatch({
                    type    : status.error,
                    payload : true,
                });

                return
            }
            
        }
}

export const SuppliersTable = (data:{}) => {
    return async (dispatch:any) => {
        dispatch({type    : sp.table,payload : data});
    }
}

export const SuppliersFilter = (filter:boolean) => {
    return async (dispatch:any) => {    
        dispatch({type:sp.filter,payload:filter});
    }
}