import { MasterCodes as MC,status } from './Types';
import { Token } from 'Services';
import { request } from 'Services/Requests/StaticMethods';
import Url from 'Services/ServerUrl';


interface param{
    per_page ?:number,
    page     ?:number,
}


export const MasterCodes = (params:param) => {
    return async (dispatch:any) => {


        dispatch({
            type    : MC,
            payload : '',
        });
    
    
        const token = Token.get();
            if(token === '' || token === null){
                dispatch({
                    type    : status.loggedIn,
                    payload : false,
                })
                return;
            }

            const a = await request({
                url     : Url.productMasterList+'?page='+params.page+'&per_page='+params.per_page,
                method  : 'GET',
                params  : false,
            })


            if (a.status === 200) {
                dispatch({
                    type    : MC,
                    payload : a,
                });
                return;
            }

            if(a.network_error){
                dispatch({
                    type    : status.error,
                    payload : true,
                });

                return
            }
    }
}
