import { Products as pr,status } from './Types';
import { Requests, Token } from '../../Services';
import { request } from 'Services/Requests/StaticMethods';
import Url from 'Services/ServerUrl';

interface params{
    page        :  number,
    per_page    :  number,
}
export const Products = (params ?:params) => {
        return async (dispatch:any) => {

            if(!params){
                params.page     = 1;
                params.per_page = 10;
            }

            dispatch({
                type    : pr.data,
                payload : '',
            });



            // await Requests.Products.show(params).then((response)=>dispatch({
            //     type    : pr.data,
            //     payload : response,
            // }));




            const token = Token.get();
            if(token === '' || token === null){
                dispatch({
                    type    : status.loggedIn,
                    payload : false,
                })
                return;
            }

            const a = await request({
                url     : Url.products+'?page='+params.page+'&per_page='+params.per_page,
                method  : 'GET',
                params  : false,
            })


            if (a.status === 200) {
                dispatch({
                    type    : pr.data,
                    payload : a,
                });
                return;
            }

            if(a.network_error){
                dispatch({
                    type    : status.error,
                    payload : true,
                });

                return
            }

        }
}

export const ProductsTable = (data:{}) => {
    return async (dispatch:any) => {
        dispatch({type    : pr.table,payload : data});
    }
}

export const ProductsFilter = (filter:boolean) => {
    return async (dispatch:any) => {    
        dispatch({type:pr.filter,payload:filter});
    }
}