import { Users as pr, status } from './Types';
import { Token } from '../../Services';
import { request } from 'Services/Requests/StaticMethods';
import Url from 'Services/ServerUrl';

interface params{
    page        :  number,
    per_page    :  number,
}

export const Users = (params ?:params) => {
        
        return async (dispatch:any) => {

            if(!params){
                params.page     = 1;
                params.per_page = 10;
            }

            dispatch({
                type    : pr.data,
                payload : '',
            });



            // await Requests.User.show(params).then((response)=>dispatch({
            //     type    : pr,
            //     payload : response,
            // }));

            const token = Token.get();
            if(token === '' || token === null){
                dispatch({
                    type    : status.loggedIn,
                    payload : false,
                })
                return;
            }

            const a = await request({
                url     : Url.user+'?page='+params.page+'&per_page='+params.per_page,
                method  : 'GET',
                params  : false,
            })


            if (a.status === 200) {
                dispatch({
                    type    : pr.data,
                    payload : a,
                });
                return;
            }

            if(a.network_error){
                dispatch({
                    type    : status.error,
                    payload : true,
                });

                return
            }

        }
}

export const UsersFilter = (filter:boolean) => {
    return async (dispatch:any) => {    
        dispatch({type:pr.filter,payload:filter});
    }
}